<template>
    <b-row class="mb-2">
        <b-col cols="2" class="todoItem__text"> {{ todo.text }} </b-col>
        <b-col cols="2" class="todoItem__text"> {{ todo.categoria }} </b-col>
        <b-col cols="1" class="todoItem__text"> {{ todo.numPaginas }} </b-col>
        <b-col cols="1" class="todoItem__text"> {{ todo.ISBN }} </b-col>
        <b-col cols="2" class="todoItem__text"> {{ todo.autor }} </b-col>
        <b-col cols="2" class="todoItem__text"> {{ todo.editorial }} </b-col>
        <b-col>
            <b-button class="d-block w-100" variant="primary" @click="goToUpdateTodo"> Editar </b-button>
            <b-button class="d-block w-100" variant="warning" @click="updateStatusTodo"> Estado </b-button>
            <b-button class="d-block w-100" variant="danger" @click="deleteTodo"> Eliminar </b-button>
        </b-col>
    </b-row>
</template>

<script>
import { mapMutations, mapActions } from 'vuex';

export default {
    name: 'TodoItem',
    props:{
        todo: {
            type: Object,
            required: true
        }
    },
    methods: {
        ...mapActions('todos', {
            _updateStatusTodo: 'updateStatusTodo',
            _deleteTodo: 'deleteTodo'
        }),
        ...mapMutations('todos', ['setTodo']),
        goToUpdateTodo(){
            this.setTodo(this.todo);
            this.$router.push({
                name: 'TodosUpdate',
                params: { id: this.todo.id }
            });
        },
        updateStatusTodo(){
            this._updateStatusTodo(this.todo);
        },
        deleteTodo(){
            this._deleteTodo(this.todo)
        }
    }
}
</script>